.block-also {
  display: none;
}

.block-also .block__title {
  margin-bottom: 42px;
}

.block-also .block__items {
  display: grid;
  grid-template: auto/1fr;
  flex: 1 100%;
}

@media only screen and (min-width: 768px) {
  .block-also {
    display: block;
  }

  .block-also .block__items {
    grid-template: auto/1fr 1fr 1fr;
  }
}